import React from 'react';
import Rebase from 're-base';

var base = Rebase.createClass({
  apiKey: "AIzaSyDqJqwjjUBFeKmdsZI65qgWxC7V_tTEMAQ",
  authDomain: "dunshiretrack.firebaseapp.com",
  databaseURL: "https://dunshiretrack.firebaseio.com",
  storageBucket: "dunshiretrack.appspot.com",
  messagingSenderId: "795262233532"

});


class UserProfiles extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      all_projects: {},
      project:{}
    };
  }
  init(){
    this.ref = base.syncState(`/projects`, {
      context: this,
      asArray: true,
      state: 'all_projects'
    });

  }
  componentDidMount(){
    this.init();
  }
  componentWillUnmount(){
    base.removeBinding(this.ref);
  }
  componentWillReceiveProps(){
    base.removeBinding(this.ref);
    this.init();
  }
  handleAddNote(newNote){
    this.setState({
      notes: this.state.notes.concat([newNote])
    })
  }


  render(){
    var projects = [];
    var cur = this.state.all_projects;
    for (var key in this.state.all_projects)
    {
      var name = this.props.bio.Name.substring(0,3);
      console.log(cur[key].Name)
      if (cur[key].Name.includes(name.substring(0,3)))
        projects.push(cur[key]);
    }
    console.log(projects)
    return (

    <div>
      <div class="row">
          <div class="col-lg-12">
              <div class="panel panel-default">
                  <div class="panel-body">
                  </div>
              </div>
          </div>
      </div>
      <div>
        <h3> User Profile </h3>
        <ul className="list-group">
          <li className="list-group-item"><b>Name: </b>{this.props.bio.Name} </li>
            <li className="list-group-item"><b>Email: </b> {this.props.bio.Email} </li>
              <li className="list-group-item"><b>Focus Area:</b> {this.props.bio["Focus Area"]} </li>
                <li className="list-group-item"><b>Skills:</b> {this.props.bio["Skills"]} </li>
                  <li className="list-group-item"><b>Title:</b> {this.props.bio["Title"]} </li>



        </ul>
      </div>
    </div>
    )
  }
};

UserProfiles.propTypes = {
  username: React.PropTypes.string.isRequired,
  bio: React.PropTypes.object.isRequired
}

export default UserProfiles;



