import React from 'react';
import ReactDOM from 'react-dom';
import { withRouter } from 'react-router';

class SearchGithub extends React.Component{
  handleSubmit(e){
    e.preventDefault();
    var firstName = ReactDOM.findDOMNode(this.refs.firstName).value;
    var lastName = ReactDOM.findDOMNode(this.refs.lastName).value;

    ReactDOM.findDOMNode(this.refs.firstName).value = '';
    ReactDOM.findDOMNode(this.refs.lastName).value = '';
    name = firstName+'_'+lastName
    this.props.router.push(`/profile/${name}`);
  }
  render(){
    return (
      <div className="col-sm-12">
        <form onSubmit={this.handleSubmit.bind(this)}>
          <div className="form-group col-sm-3">
            First name:<input type="text" className="form-control" ref="firstName" />
          </div>
          <div className="form-group col-sm-3">
            Last name: <input type="text" className="form-control" ref="lastName" />
          </div>
          <div className="form-group col-sm-5">
            <button type="submit" className="btn btn-block btn-primary">Enter Your Name </button>
          </div>
        </form>
      </div>
    )
  }
};

SearchGithub.contextTypes = {
  router: React.PropTypes.object.isRequired
};

export default withRouter(SearchGithub);



