import React from 'react';
import Repos from './Github/Repos';
import UserProfile from './Github/UserProfile';
import Notes from './Notes/Notes';
import helpers from '../utils/helpers';
import Rebase from 're-base';

var base = Rebase.createClass({
  apiKey: "AIzaSyDqJqwjjUBFeKmdsZI65qgWxC7V_tTEMAQ",
  authDomain: "dunshiretrack.firebaseapp.com",
  databaseURL: "https://dunshiretrack.firebaseio.com",
  storageBucket: "dunshiretrack.appspot.com",
  messagingSenderId: "795262233532"

});

class Profile extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      notes: [],
      bio: {},
      repos: []
    };
  }
  init(){
    this.ref = base.syncState(`/employees/${this.props.routeParams.username}`, {
      context: this,
      state: 'bio'
    });

  }
  componentDidMount(){
    this.init();
  }
  componentWillUnmount(){
    base.removeBinding(this.ref);
  }
  componentWillReceiveProps(){
    base.removeBinding(this.ref);
    this.init();
  }
  handleAddNote(newNote){
    this.setState({
      notes: this.state.notes.concat([newNote])
    })
  }
  render(){
    var username = this.props.routeParams.username;
    return (
      <div className="row">
        <div className="col-md-4">
          <UserProfile username={username} bio={this.state.bio}/>
        </div>
      </div>
    )
  }
};

Profile.contextTypes = {
  router: React.PropTypes.object.isRequired
};

export default Profile;



